html {
    font-size: 100%;
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Roboto', sans-serif;
    line-height: 1.3;
}

ul {
    text-decoration: none;
    list-style-type: none;
}

input,
button {
    outline: none;
}