.chat-widget {
    width: 4.375rem;
    height: 4.375rem;
    position: fixed;
    padding: 0.625rem;
    bottom: 0;
    z-index: 999999;
    &__icon-wrap {
        width: 100%;
        height: 100%;
    }
    &__icon {
        width: 100%;
        height: 100%;
        border-radius: 2.1875rem;
        background-image: url("../../images/hasaki-logo.png");
        background-size: cover;
        background-color: black;
        background-color: color(light-green);
        cursor: pointer;
    }
}

.chat-tab {
    background-color: #fff;
    width: 20.5rem;
    height: 28.4375rem;
    position: fixed;
    margin-bottom: 3.125rem;
    bottom: 0;
    z-index: 999999;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    &__title {
        width: 100%;
        display: flex;
        flex-direction: column;
        &__group {
            min-height: 3rem;
            color: white;
            border-radius: 0.75rem 0.75rem 0 0;
            padding: 0 0.75rem;
            background-color: color(green);
            align-items: center;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            &>h2 {
                font-size: 0.875rem;
                font-weight: unset;
            }
        }
        &__search-bar {
            width: 100%;
            padding: 5px 0.75rem;
            height: 3.3125rem;
            z-index: 99999999;
            border: 1px solid color(green);
            border-bottom: none;
            &__wrap-input {
                & input {
                    width: 100%;
                    height: 20px;
                    // border-radius: 25px;
                    border: 1px solid;
                }
            }
            &__result {
                text-align: center;
                font-size: 13px;
                margin-top: 3px;
            }
        }
    }
    &__actions {
        display: flex;
        flex-direction: row;
        width: 4.6875rem;
        height: 100%;
        align-items: center;
        justify-content: space-between;
        & .items {
            height: 0.9375rem;
        }
        & .items:hover {
            cursor: pointer;
            color: red;
        }
    }
    &__posts {
        width: 100%;
        height: 100%;
        border: 1px solid color(green);
        border-bottom: none;
        overflow-y: scroll;
        overflow-x: hidden;
        & ul li {
            &:hover .sent-group__actions,
            &:hover .reply-group__actions {
                visibility: visible;
                & .post__controls__icon-btn:hover {
                    cursor: pointer;
                }
            }
            &:nth-last-child(1) {
                margin-bottom: 0.625rem;
            }
            & p {
                display: inline-block;
                padding: 0.625rem 0.9375rem;
                border-radius: 1.25rem;
                max-width: 12.8125rem;
                line-height: 130%;
                word-break: break-word;
            }
        }
    }
}

.sent-post {
    margin-top: 0.625rem;
    &__time {
        width: 100%;
        font-size: 11px;
        color: #8a8d91;
        text-align: center;
    }
}

.sent-group {
    display: flex;
    justify-content: flex-start;
    &__avatar {
        width: 2rem;
        position: relative;
        padding: 0 0.5rem;
        margin-right: 5px;
        &__icon {
            position: absolute;
            bottom: 0;
            & img {
                width: 100%;
            }
        }
    }
    &__content {
        width: 12.5rem;
        background-color: color(light-green);
        border-radius: 0.625rem;
    }
    &__actions {
        width: 3.125rem;
        display: flex;
        visibility: hidden;
        align-items: center;
        padding: 0.3125rem;
    }
}

.reply-post {
    margin-top: 0.625rem;
    &__time {
        width: 100%;
        font-size: 11px;
        color: #8a8d91;
        text-align: center;
    }
}

.reply-group {
    display: flex;
    justify-content: flex-end;
    &__avatar {
        width: 2rem;
        position: relative;
        padding: 0 0.5rem;
        &__icon {
            position: absolute;
            bottom: 0;
            & img {
                width: 100%;
            }
        }
    }
    &__content {
        width: 12.5rem;
        background-color: color(light-blue);
        border-radius: 0.625rem;
    }
    &__actions {
        width: 3.125rem;
        padding: 0.3125rem;
        visibility: hidden;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
    }
}

.add-msg {
    width: 100%;
    border: 1px solid color(blue);
    border-radius: 0 0 0.75rem 0.75rem;
    &__container {
        width: 95%;
    }
    &__reply {
        padding: 0.625rem 0.9375rem 0.1875rem;
        border-top: 1px solid color(light-blue);
        font-size: 0.8125rem;
        display: flex;
        &__content {
            text-overflow: ellipsis;
            overflow: hidden;
        }
    }
    &__wrap {
        display: flex;
        height: 3.3125rem;
        justify-content: space-between;
        text-align: center;
        padding: 0.25rem 0.75rem;
        align-items: center;
    }
    &__camera {
        width: 15%;
        font-size: 1.5625rem;
        position: relative;
        & input[type=file] {
            position: absolute;
            width: 100%;
            left: 0;
            font-size: 1.5625rem;
            opacity: 0;
        }
        &:hover {
            color: color(light-blue);
            cursor: pointer;
        }
    }
    &__message {
        width: 70%;
        height: 2.3125rem;
        border-radius: 1.25rem;
        font-size: 1.5rem;
        padding-left: 5px;
    }
    &__send {
        width: 15%;
        font-size: 1.5625rem;
        &:hover {
            color: #129CF7;
            cursor: pointer;
        }
    }
    &__cancel {
        width: 5%;
        text-align: center;
        &:hover {
            cursor: pointer;
        }
    }
}

// CALL MODAL
.modal {
    position: fixed;
    z-index: 999999999;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.4);
    &-content {
        background-color: #fefefe;
        margin: 5% auto;
        padding: 1.25rem;
        border: 1px solid black;
        width: 80%;
        height: 80%;
        border-radius: 0.625rem;
    }
}

// CALL VIEW
.call-view {
    width: 100%;
    height: 100%;
    &__controls-container {
        height: 10%;
    }
    &__controls {
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
        width: 30%;
        &__icon-btn {
            padding: 5px;
            border-radius: 5px;
            font-size: 32px;
            &:hover {
                transform: scale(1.25);
            }
            &.important {
                background-color: #FF3346;
                color: white;
            }
        }
    }
    &__tracks {
        height: 90%;
    }
}

.remote-track {
    &--container {
        text-align: center;
        margin: 0 auto;
        width: 18.75rem;
        padding-top: 1.25rem;
    }
    &--picture {
        height: 18.75rem;
        border: 0.625rem solid;
        border-radius: 13.125rem;
        font-size: 14.125rem;
    }
}

.input-status-container {
    .input-status {
        &>i {
            font-size: 20.5rem;
        }
    }
}

#localAudio,
#remoteAudio {
    display: none;
}

@media only screen and (max-width: 600px) {
    .chat-tab {
        width: 90%;
        margin: 10px !important;
    }
}

// CALL ALERT
.call-alert {
    width: 100%;
    height: 100%;
    border: 1px solid color(green);
    border-bottom: none;
    padding: 15px;
    text-align: center;
    &__user {
        display: flex;
        align-items: center;
        flex-direction: column;
        &__avatar {
            border: 1px solid;
            border-radius: 50%;
            width: 50%;
            padding: 5px;
            & img {
                border-radius: 50%;
            }
        }
        &__name {
            padding: 10px;
            font-weight: bold;
            font-size: 20px;
        }
    }
    &__controls {
        display: flex;
        justify-content: space-evenly;
        margin-top: 10%;
        &__item {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            padding: 10px;
            border-radius: 24px;
            font-size: 20px;
            width: 8.125rem;
            border: none;
            &:hover {
                cursor: pointer;
            }
            &__icon {
                &__cancel {
                    transform: rotate( 135deg);
                }
                &__accept {}
            }
        }
    }
    &__time {
        color: color(blue);
    }
}

#acceptBtn {
    background-color: color(light-green);
    color: white;
    &:focus {
        box-shadow: 0 0 2pt 1pt #000;
    }
}

#cancelBtn {
    background-color: color(alert);
    color: white;
    &:focus {
        box-shadow: 0 0 2pt 1pt #000;
    }
}